import { Component, ViewEncapsulation } from '@angular/core';
import { NzMessageService } from 'ng-zorro-antd/message';
import { UploadChangeParam, UploadFile } from 'ng-zorro-antd/upload';
import * as XLSX from "xlsx";
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Observable, Observer } from 'rxjs';

type AOA = any[][];
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class AppComponent {
  validateForm: FormGroup;
  subCategory1Data = [
    {
      value: 1,
      label: 'Retail Store'
    },
    {
      value: 2,
      label: 'Dept. Store'
    },
    {
      value: 3,
      label: 'ECOMMERCE FASHION'
    },
    {
      value: 4,
      label: 'Boutique'
    }
  ];
  subCategory2Data = [
    {
      value: 1,
      label: 'Casual Wear'
    },
    {
      value: 2,
      label: 'Kids and Baby Wear'
    },
    {
      value: 3,
      label: 'Islamic Clothing'
    },
    {
      value: 4,
      label: 'Foot Wear'
    },
    {
      value: 5,
      label: 'Formal Wear'
    }
  ];
  category1SuggestionsData = [];
  category2SuggestionsData = [];

  title = 'AI-mall';
  listOfData: any[] = [
    {
      key: '1',
      name: 'John Brown',
      age: 32,
      address: 'New York No. 1 Lake Park'
    },
    {
      key: '2',
      name: 'Jim Green',
      age: 42,
      address: 'London No. 1 Lake Park'
    },
    {
      key: '3',
      name: 'Joe Black',
      age: 32,
      address: 'Sidney No. 1 Lake Park'
    }
  ];
  isUploading = false;
  isDone = false;
  data: any;
  headData: any; // excel row header
  uploadExcel = false;
  selectedSubCategory1: any;
  selectedSubCategory2: any;
  isCategorized = false;
  hasSearched = false;
  store: string = '';
  categoryNotFound = false;
  categoryNotFoundErrorMessage = '';
  createManual = false;
  isDisabledSwitcher = false;

  private _index: number = 1;

  constructor(private msg: NzMessageService, private fb: FormBuilder) {}

  ngOnInit(): void {
    this.mappingCategories();
    this.validateForm = this.fb.group({
      name: [null, [Validators.required]],
      subCategory1: [null, [Validators.required]],
      subCategory2: [null, [Validators.required]],
    });
  }

  mappingCategories() {
    this.category1SuggestionsData = this.subCategory1Data.map((data) => data.label);
    this.category2SuggestionsData = this.subCategory2Data.map((data) => data.label);
  }

  submitForm(): void {
    for (const i in this.validateForm.controls) {
      if (this.validateForm.controls[i]) {
        this.validateForm.controls[i].markAsDirty();
        this.validateForm.controls[i].updateValueAndValidity();
      }
    }
    // category submission here ..
  }

  categorizeStore() {
    this._resetForm();
    this.isCategorized = true;
    this.isDisabledSwitcher = true;
    this._shuffle(this.category1SuggestionsData);
    this._shuffle(this.category2SuggestionsData);

    const intervalHandle = setInterval(() => {
        const category1 = this._index++ % this.category1SuggestionsData.length;
        this.selectedSubCategory1 = this.category1SuggestionsData[category1];

        const category2 = this._index++ % this.category2SuggestionsData.length;
        this.selectedSubCategory2 = this.category2SuggestionsData[category2];
    }, 50);

    setTimeout(() => { 
        clearInterval(intervalHandle);
        if (this.store.includes('warung')) {
          this.categoryNotFound = true;
          this.categoryNotFoundErrorMessage =
          'Oops! your Store does not have any suggestion category yet. Please input your store info manually first';
        }
        this.isCategorized = false;
        this.hasSearched = true;
        this.isDisabledSwitcher = false;
    }, 3000);
  }

  private _resetForm() {
    this.hasSearched = false;
    this.categoryNotFound = false;
    this.categoryNotFoundErrorMessage = '';
    this.createManual = false;
  }

  private _shuffle(array) {

    var currentIndex = array.length, temporaryValue, randomIndex;

    // While there remain elements to shuffle...
    while (0 !== currentIndex) {

      // Pick a remaining element...
      randomIndex     = Math.floor(Math.random() * currentIndex);
      currentIndex   -= 1;

      // And swap it with the current element.
      temporaryValue      = array[currentIndex];
      array[currentIndex] = array[randomIndex];
      array[randomIndex]  = temporaryValue;
    }

    return array;
  }
  
  createManualStore() {
    this.createManual = true;
    this.validateForm.controls['name'].setValue(this.store);
  }

  handleChange(info: UploadChangeParam): void {
    const status = info.file.status;
    this.isUploading = true;
    this.isDisabledSwitcher = true;
    if (status !== 'uploading') {
      this.isUploading = false;
    }

    if (status === 'done') {
      this.isUploading = false;
      this.isDone = true;
      this.isDisabledSwitcher = false;
      this._getFileBuffer(info.file.originFileObj, (file: any) => {
        this.handleExcel(file);
      });
      this.msg.success(`${info.file.name} file uploaded successfully.`);
    } 
    
    if (status === 'error') {
      this.isUploading = false;
      this.isDone = true;
      this.isDisabledSwitcher = false;
      this.msg.error(`${info.file.name} file upload failed.`);
    }
  }

  private _getFileBuffer(file: File, callback: any): void {
    const reader = new FileReader();
    reader.addEventListener('load', () => callback(reader.result));
    reader.readAsArrayBuffer(file);
  }

  handleExcel(bstr: any) {
    const buffer = new Uint8Array(bstr);
    const wb: XLSX.WorkBook = XLSX.read(buffer, { type: 'array' }); 

    /* grab first sheet */
    const wsname: string = wb.SheetNames[0];
    const ws: XLSX.WorkSheet = wb.Sheets[wsname];

    /* save data */
    this.data = <AOA>(
      XLSX.utils.sheet_to_json(ws, { header: 1 })
    );

    this.headData = this.data[0];
    this.data = this.data.slice(1); // remove first header record

    const ws2: XLSX.WorkSheet = wb.Sheets[wb.SheetNames[1]];
    this.readDataSheet(ws2, 10);
  }

  private readDataSheet(ws: XLSX.WorkSheet, startRow: number) {
    /* save data */
    let datas = <AOA>(
      XLSX.utils.sheet_to_json(ws, { header: 1, raw: false, range: startRow })
    );
    let headDatas = datas[0];
    datas = datas.slice(1); // remove first header record

    for (let i = 0; i < this.data.length; i++) {
      this.data[i][this.headData.length] = datas.filter(
        x => x[12] == this.data[i][0]
      );
    }
  }
}
